<template>
  <div>
    <div class="title"></div>
    <m-button type="success" @click="handleSuccess"></m-button>
    <div class="title"></div>
    <m-button type="danger" @click="handleWarning"></m-button>
  </div>
</template>

<script>
import MButton from '@/components/m-button/m-button'

export default {
  components: {
    MButton
  },
  methods: {
    handleSuccess () {
      this.$message.success({
        message: ''
      })
    },
    handleWarning () {
      this.$message.error({
        message: ''
      })
    }
  }
}
</script>

<style scoped>
.title {
  margin: 20px 0;
}
</style>
