<template>
  <div class="profile">
    <m-dialog
      :dialogVisible.sync="dialogVisible"
      :title="title">
      <div>{{ruleForm.id}}</div>
      <div>{{ruleForm.sex}}</div>
      <div>{{ruleForm.count}}</div>
    </m-dialog>
    <div class="profile-header">
      <m-table
        :dataList="dataList"
        :titleList="titleList">
      </m-table>
    </div>
  </div>
</template>

<script>
import MTable from '@/components/m-table/m-table'
import MDialog from '@/components/m-dialog/m-dialog'

export default {
  components: {
    MTable,
    MDialog
  },
  data () {
    return {
      dataList: [],
      title: '',
      dialogVisible: false,
      ruleForm: {
        id: '',
        sex: 0,
        count: ''
      }
    }
  },
  created () {
    let list = []
    for (let i = 1; i < 50; i++) {
      list.push({
        id: i,
        sex: Math.random() > 0.5 ? 0 : 1,
        count: 100 + i
      })
    }
    this.dataList = list
    this.titleList = [
      {
        key: 'id',
        text: ''
      },
      {
        key: 'sex',
        text: '',
        filter: (item) => {
          return item.sex ? '' : ''
        }
      },
      {
        key: 'count',
        text: ''
      },
      {
        key: 'html',
        text: '',
        width: 150,
        render (h, column) {
          let handlerClick = () => {
            this.handleDelete(column)
          }
          return <el-button on-click={handlerClick}></el-button>
        },
      }
    ]
  },
  methods: {
    // 
    handleDelete (item) {
      this.title = ''
      this.dialogVisible = true
      this.ruleForm = item
    }
  }
}
</script>

